<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process291"></a>

<p><br></p>
<p><span class="rvts17">猩红王子（</span><span class="rvts50">The Red Prince</span><span class="rvts17">）：</span></p>
<p class="rvps2"><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1554.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts81">故事背景：</span>
<br><span class="rvts21">猩红王子是蜥蜴王国的贵族，后来由于喜欢和恶魔一起玩耍，被自己的国家而抛弃，驱逐在外，长期流浪。猩红王子想要重返王国，并掌控最高的权利。</span>
<br><span class="rvts20"></span>
<br><span class="rvts81">支线任务：</span>
<br><span class="rvts21">欢乐堡部分：</span>
<br><span class="rvts20">1. </span><span class="rvts21">与欢乐报的商人斯汀泰尔（</span><span class="rvts20">Stingtail</span><span class="rvts21">）谈话，他会给你提及阿玛蒂亚避难所中的巴哈拉（</span><span class="rvts20">Bahara</span><span class="rvts21">）。注意，该</span><span class="rvts20">NPC</span><span class="rvts21">是</span><span class="rvts20"></span><span class="rvts21">一个被囚禁的精灵</span><span class="rvts20"></span><span class="rvts21">支线中的重要</span><span class="rvts20">NPC</span><span class="rvts21">，最终会被刺杀掉（或被塞碧莉杀掉），因此尽可能在猩红王子与他对话前，不要使用塞碧莉在队伍中与它谈话。</span>
<br><span class="rvts20">2. </span><span class="rvts21">来到阿玛蒂亚避难所，找到巴哈拉（守门的蜥蜴），与他交谈，他会让你找浮木城的布拉哈姆斯（</span><span class="rvts20">Brahamos</span><span class="rvts21">）。</span>
<br><span class="rvts21">死神海岸部分：</span>
<br><span class="rvts20">3. </span><span class="rvts21">到达浮木城之后，与酒吧地下层的甘福（</span><span class="rvts20">Ghangh</span><span class="rvts21">）谈话，他会告诉你布拉萨姆斯不在浮木城，而是去了圣骑士桥头并且有人跟踪他。</span>
<br><span class="rvts20">4. </span><span class="rvts21">接着，来到石墓花园，在一处有两头石火龙相互喷火的地方，使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，可以找到布拉哈姆斯的灵魂。布拉哈姆斯是蜥蜴种族的梦者，他会告诉猩红王子接下来要做的事情。</span>
<br><span class="rvts20">5. </span><span class="rvts21">在锯木厂东北部，有一处蜥蜴聚集的地方，那里是猩红公主萨哈（</span><span class="rvts20">Sadha</span><span class="rvts21">）的营地。让猩红王子与猩红公主对话，将会推动他的任务剧情。接着，一些蜥蜴会埋伏攻击猩红王子，成功解围后，发现萨哈不见了。</span>
<br><span class="rvts21">无名岛部分：</span>
<br><span class="rvts20">6. </span><span class="rvts21">在无名岛，找到黑环领袖</span><span class="rvts20"></span><span class="rvts21">黄瘦男人</span><span class="rvts20"></span><span class="rvts21">，在他的居住山洞中，可以找到一面黑镜，黑镜中会显示猩红公主的现状。</span>
<br><span class="rvts20">7. </span><span class="rvts21">在无名岛的蜥蜴神殿废墟，找到暗影王子，他会告诉你是他密谋了在死神海岸公主营地的埋伏计划。然而，他解释说这样做的目的是为了阻止一只龙的诞生，他认为这只龙会给蜥蜴种族带来灾难。他还让猩红王子杀掉萨哈。</span>
<br><span class="rvts21">阿克斯部分：</span>
<br><span class="rvts20">8. </span><span class="rvts21">来到阿克斯后，在支线</span><span class="rvts20"></span><span class="rvts21">领事会</span><span class="rvts20"></span><span class="rvts21">中，可以找到一个到达猩红公主萨哈处的传送门，到达那里后，猩红王子的全部支线将完结。你可以选择杀掉猩红公主，或解救她。解救猩红公主需要</span><span class="rvts20"></span><span class="rvts21">破誓者</span><span class="rvts20"></span><span class="rvts21">（整个游戏只有两把，一把需要找部件合成，详见</span><span class="rvts20"></span><span class="rvts21">自由之钥</span><span class="rvts20"></span><span class="rvts21">支线；另一把在林德</span><span class="rvts20"></span><span class="rvts21">科姆藏宝室，详见</span><span class="rvts20"></span><span class="rvts21">林德</span><span class="rvts20"></span><span class="rvts21">科姆藏宝室</span><span class="rvts20"></span><span class="rvts21">支线。）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



